import Vue from 'vue';
import Vuex from 'vuex';

import entry from '@/store/modules/entry'
import sessionConfiguration from '@/store/modules/sessionConfiguration'
import sessionStep from '@/store/modules/sessionStep'

Vue.use(Vuex);

export default new Vuex.Store({
  modules:{
    entry,
    sessionConfiguration,
    sessionStep,
  },
  state: {
    dashboardContent:'content-configuration',
  },
  mutations: {
    SETDASHBOARDCONTENT(state,value){      
      state.dashboardContent = value;
      console.log
    }
  },
  actions: {
    setDashboardContent(context, value){
      context.commit('MUTATESTEPDATA',value)
    },
  },
});
